	methods:{
		toWhoLikeMe(){
			uni.switchTab({
				url:'../message/Message',
				sucess(){
					console.log("sucess Message");
				},
				fail(err){
					console.log("fail Message");
					console.log(err);
				},
				complete(){
					uni.setStorage({
						key:"messageCheckValue",
						data:"like"
					});
					console.log("complete")
				}
			})
		},
		toMemberCenter(){
			uni.navigateTo({
				url:'subPages/memberCenter',
				success() {
					console.log("success toMemberCenter");
				},
				fail(){
					console.log("fail toMemberCenter");
				}
			})
		},
		toImproveData(){
			uni.navigateTo({
				url:'subPages/ImproveData',
				success() {
					console.log("success toImproveData");
				},
				fail(){
					console.log("fail toImproveData");
				}
			})
		},
		toTripartiteAccount(){
			uni.navigateTo({
				url:'subPages/tripartiteAccount',
