		toLogin(){
			uni.showModal({
			    title:'确定退出',
			    content:'您确定要退出吗?',
			    success:function(res){
			        if(res.confirm){
			            uni.reLaunch({//加载登陆页面
							url:'../login/login',
							success() {
								console.log("success toLogin");
							},
							fail(){
								console.log("fail toLogin");
							}
						})
			        }
					else if(res.cancel){
			            console.log('用户点击取消');
			        }
			    }
			});				
		},
